import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import Router from '../../router/scmp_router'
import routerInit from '../../router/router-actions'
routerInit(Router);
Vue.use(VueRouter)
/* eslint-disable no-new */
new Vue({
  el: '#scmp',
  router:Router,
  template: '<App/>',
  components: { App }
}).$mount('#scmp')



