import Router from "vue-router"
import VueResource from 'vue-resource'
import Vue from 'vue'
import App from '../module/scmp/App'
import page1 from '../module/scmp/views/page1'
import login from '../module/scmp/views/login'
Vue.use(Router)
Vue.config.debug = true
Vue.use(VueResource)
const router = new Router({
  mode: 'hash',
  base: __dirname,
  routes: [
    {
      path: '/scmp/login',
      component: login,
      name:'login'
    },
    {
      path: '/scmp/page1',
      component: page1,
      name:'page1'
    }
  ]
})
export default router



